/* breadcrumbs */
.breadcrumbs {
	color: $text-color-light;
	padding: 20px $text-layout-space;
	border-bottom: 1px solid $border-color;
	margin-bottom: -1px;

	@include media('<tab-lg') {
		display: none; }

	&:last-child {
		margin-bottom: 0; } }

.breadcrumbs__list {
	&, .#{$user-content} & {
		padding: 0;
		margin: 0;
		list-style: none;

		li {
			padding: 0 6px 0 0;
			margin: 0;

			&::before, &:first-child::before {
				content: none; } } }

	cursor: default;
	overflow: hidden;
	@include font(1.6rem, round-float(20/16, 2));

	.layout-article & {
		max-width: none; }

	li {
		display: inline;
		position: relative;

		&::after {
			display: inline-block;
			vertical-align: baseline;
			content: '>';
			border: none;
			color: #666;
			padding-left: 10px; }

		&:last-child::after {
			content: none; } }

	a {
		padding-top: 0.5em;
		color: #666;
		text-decoration: none;
		cursor: pointer;

		span {
			border-bottom: 1px solid rgba(white, 0.3); }

		.no-touchevents &:hover {
			color: $base-color;

			span {
				border-color: rgba($base-color, 0.3); } } } }

/* breadcrumbs end */

/* files links */
.file {
	$file-icon-size: 42px;
	@include display-inline-block-top;
	min-height: $file-icon-size;
	padding: 0.5em;
	@include font(16px, 20px);
	color: $text-color;
	text-decoration: none;

	@include clear;

	svg {
		float: left;
		@include size($file-icon-size); }


	& > span, & > em {
		display: block;
		margin-left: #{$file-icon-size};
		padding-left: 1em; }

	& > em {
		display: block;
		font-size: 1.4rem;
		color: $text-color-light; }

	.no-touchevents &:hover {
		background-color: $base-color;

		& > em {
			color: $bg-color-base; } } }

.st-bg {
	fill: #e2e5e7; }

.st-corner {
	fill: #b0b7bd; }

.st-shadow {
	fill: #cad1d8; }

.st-text {
	fill: #fff; }

/* files links end */

/* feature list */
.feature-list {
	margin-bottom: $bottom-space--md;

	&:last-child {
		margin-bottom: 0; } }

.feature-list__item {
