	@include clear;

	&:last-child {
		margin-bottom: 0; } }

.feature-list__img {
	float: left;
	width: 25%;
	margin: 0.3em $bottom-space $bottom-space--sm 0;

	@include media("<mob-lg") {
		width: auto;
		float: none;
		margin-right: 0; } }

.feature-list__content {
