/* feature list end */

// pagination
$pagination-space: 4px 4px; // v h
$pagination-font-size: 14px;
$pagination-item-size: 40px 40px; // w h
$pagination-border-width: 1px;

/* pagination */
.pagination {
	overflow: hidden;
	margin-bottom: $bottom-space--lg;
	border: solid $border-color;
	border-width: 1px 0;
	@include font-bold;
	color: inherit;
	text-align: center;
	cursor: default;
	@include push-auto;
	@include clear;

	&:last-child {
		margin-bottom: 0; } }

.pg-left {
	float: left; }

.pg-right {
	float: right; }

.pg-prev, .pg-next {
	position: relative;
	display: block;
	@include size(nth($pagination-space, 1)*2+(nth($pagination-item-size, 2))+$form-border-width);
	border: $pagination-border-width solid $border-color;
	margin: -$pagination-border-width 0;

	span {
		display: none; } }

.pg-prev {
	margin-left: -$pagination-border-width; }

.pg-next {
	margin-right: -$pagination-border-width; }

.pg-prev, .pg-next {
	color: #999;
	text-decoration: none;

	// hover
	.no-touchevents &:hover {
		color: $base-color; }

	// disabled
	&.disabled, .no-touchevents &.disabled {
		cursor: default;
		color: #dedede; } }

.pg-prev-arr, .pg-next-arr {
	position: absolute;
	@include position(50%, 50%);
	@include size(16px);
	margin: -8px 0 0 -8px;
	letter-spacing: 0;

	svg {
		display: block;
		@include size(100%); }

	path {
		fill: currentColor; } }

.pg-list {
	@include display-inline-block-middle;

	&, .#{$user-content} & {
		margin: 0;

		li {
			margin: nth($pagination-space, 1) nth($pagination-space, 2);
			padding: 0;

			&:before {
				content: none; } } }

	& {
		@include clear-spacing; }

	li {
		@include display-inline-block-top;
		text-align: center;

		a, & > span {
			@include display-inline-block-top;
			color: inherit;
			background: transparent;
			min-width: nth($pagination-item-size, 1);
			height: nth($pagination-item-size, 2);
			padding: 0 3px;
			border-radius: 50%;
			@include font($pagination-font-size, nth($pagination-item-size, 1));
			letter-spacing: normal;
			text-transform: uppercase; }

		a {
			text-decoration: none;
			cursor: pointer;

			// hover
			.no-touchevents &:hover {
				color: $base-color; } }

		// active
		&.active span {
			color: #999; } } }

/* pagination end */

/* social networks */
.social-networks {
	margin-bottom: $bottom-space;
	@include clear;

	&:last-child {
		margin-bottom: 0; }

	.footer & {
		text-align: center; } }

.social-networks__list {
	position: relative;
	display: inline-block;
	vertical-align: top;
	@include font(14px, 1.5);
	letter-spacing: normal;
	margin: 0 -5px -10px; }

.social-networks__item {
