/* products preview */
.products__inner {
	position: relative;
	letter-spacing: $letter-spacing;
	z-index: 1;

	.touchevents & {
		border: 1px solid #ccc; } }

.products__animation-wrap {
	//position: relative
	//+ratio(338, 389)
	$prop: margin, transform;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed; } }

.products__align {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
 }	//box-shadow: inset 0 0 0 nth($products-inner-space, 2) #fff, inset 0 -60px 0 #fff, inset 0 60px 0 #fff

.product__shadow {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: -1;
	box-shadow: 0 0 #{$products-shade-size} rgba(0, 0, 0, 0.2);
	pointer-events: none;
	will-change: opacity;
	opacity: 0;
	transition: {
		property: opacity;
		duration: $animation-speed; } }

.products__inset {
	display: block;
	position: relative;
	text-decoration: none;
	color: inherit;
	transition: none; }

.products__spacer {
	position: relative;
	@include ratio(338, 389);

	// Если блок не нужно масштабировать пропорционально
	//min-height: 389px
 }	//height: 100%

// фото продукта выносим за пределы блока,
// который увеличивается на ховер,
// потому что, если изображение находится внутри,
// то при увеличении оно дергается
.products__visual-wrap {
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	$prop: margin, transform;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed; } }

.products__visual {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	@include ratio(338, 389);
	background-color: #a3f7ff; }

.products__figure {
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	@include size(100%);
	padding: nth($products-inner-space, 1)+40 nth($products-inner-space, 2);
	//max-width: 320px // ограничиваем ширину изображения продукта
	//height: 100%
	//+push-auto
	text-decoration: none;
	color: inherit;

	//&::before
	//include pseudo-block-relative()
 }	//include ratio(338, 389)

.products__img {
	height: 100%;
	transition: {
		property: width, height, top, left;
		duration: $animation-speed;
		timing-function: ease-in-out; }

	.no-image & {
		background: url(../../img/empty.png) no-repeat center; }

	img {
		display: block;
		width: 100%;
		height: 100%;
		max-width: none;
		@include object-fit(scale-down); } }

// info
.products__info {
	position: absolute;
	left: nth($products-inner-space, 2);
	bottom: nth($products-inner-space, 1);
	min-height: 30px;
	max-width: calc(50% - #{nth($products-inner-space, 2)});
	line-height: 1; }

.products__info__brand {
	@include font-bold;
	@include font(1.3rem, round-float(16/13, 2)); }

.products__info__model {
	@include font-regular;
	@include font(1.2rem, round-float(14/12, 2));
	text-transform: uppercase; }

// price
.products__price {
	position: absolute;
	right: nth($products-inner-space, 2);
	bottom: nth($products-inner-space, 1);
	margin-bottom: -8px;
	min-height: 38px;
	max-width: calc(50% - #{nth($products-inner-space, 2)});
	text-align: right;
	line-height: 1; }

.products__price__share {
	white-space: nowrap; }

.products__price__old {
	@include display-inline-block-top;
	@include font(1.3rem, round-float(16/13, 2));
	color: #666;
	text-decoration: line-through;
	margin-right: 5px; }

.products__price__old__val {
	@include font-bold; }

.products__price__old__unit {
	@include font-bold; }

.products__price__sale {
	@include display-inline-block-top;
	position: relative;
	padding: 0 0.5rem 0 1.1rem;
	min-width: 34px;
	@include font-bold;
	@include font(1.0rem, round-float(16/10, 2));
	color: white;
	background: #fff url(../../img/bg-sale.png) no-repeat;
	background-size: 100% 100%;

	&::before {
		content: "";
		display: block;
		position: absolute;
		left: 0.4rem;
		top: 50%;
		margin-top: -1px;
		@include size(2px);
		background-color: currentColor; } }

.products__price__cur {
	@include font-bold;
	@include font(18px, 22px); }

.products__price__cur__unit {
	font-size: 14px; }

// likes
.products__likes {
	display: inline-block;
	position: absolute;
	right: nth($products-inner-space, 2);
	top: nth($products-inner-space, 1);
	overflow: hidden;
	width: 25px;
	height: 23px;
	background: url(../../img/like.png) no-repeat;
	text-indent: -9999px;

	&:hover {
		background-position: 0 -43px; }

	&.active {
		background-position: 0 -86px; } }

// labels
.products__labels {
	position: absolute;
	left: 0;
	top: nth($products-inner-space, 1);
	//transform: translate(34px, 0)
	//transform: translate3d(34px, 0, 0)
	transition: {
		property: opacity, transfrom, left;
		duration: $animation-speed; }

	.slick-slide & {
		will-change: opacity, transfrom;
		//opacity: 0
		transform: translate(#{nth($products-inner-space, 2)+nth($products-outer-space, 2)}, 0);
		transform: translate3d(#{nth($products-inner-space, 2)+nth($products-outer-space, 2)}, 0, 0); }

	.slick-slide.slick-active & {
		transform: translate(0, 0);
		transform: translate3d(0, 0, 0);
 } }		//opacity: 1

.products__label, .p-filters-share {
	position: relative;
	@include padding-horizontal(10px);
	@include font-bold;
	text-align: center;
	text-transform: uppercase;
	color: #fff;
	background-color: #f70106;

	&::before {
		content: "";
		display: block;
		position: absolute;
		left: 0;
		top: 100%;
		@include size(0);
		border: solid transparent;
		border-top-color: #880002; } }

.products__label {
	@include font(14px, 25px);
	height: 25px;
	margin-bottom: 16px;
	min-width: 82px;

	&::before {
		border-width: 10px 0 0 14px; } }

.p-filters-share {
	display: inline-block;
	vertical-align: top;
	margin: 2px 0 -2px;
	@include font(10px, 18px);
	height: 18px;
	letter-spacing: 0.1em;
	min-width: 68px;

	&::before {
		border-width: 6px 0 0 10px; } }

.products__label--sale, .p-filters-share--sale {
	background-color: #f70106;

	&::before {
		border-top-color: #880002; } }

.products__label--hit, .p-filters-share--hit {
	background-color: #ffa200;

	&::before {
		border-top-color: #ff7903; } }

// tags
.products__tags {
	position: absolute;
	right: 60px;
	top: nth($products-inner-space, 1);
	@include font(1.4rem);
	color: #666;
	opacity: 0;
	transition: {
		property: opacity;
		duration: $animation-speed; }

	.touchevents & {
		opacity: 1; } }

.products__tag {
	white-space: nowrap;

	i, span {
		@include display-inline-block-middle; }

	i {
		padding-right: 5px; }

	span {
		@include truncate(140px); } }

// hover
.no-touchevents .products__inner:hover {
	z-index: 5;

	.products__animation-wrap, .products__visual-wrap {
		transform: translate(0, -$products-rang);
		transform: translate3d(0, -$products-rang, 0);
		margin-left: -$products-rang;
		margin-right: -$products-rang; }

	.product__shadow {
		opacity: 1; }

	.products__tags {
		opacity: 1; }

	.products__labels {
		//transform: translate(0, 0)
		//transform: translate3d(0, 0, 0)
		left: -14px; } }

/* products preview end */

/* products list */
