/* similar slider */
// slider
.similar-slider {
	position: relative;

	.no-touchevents & {
		&::before, &::after {
			content: "";
			display: block;
			position: absolute;
			top: 0;
			height: 100%;
			width: round($products-rang*1.5);
			background-color: white;
			z-index: 1; }

		&::before {
			left: 0; }

		&::after {
			right: 0; } } }

.similar-slider__list {
	// before slide load
	white-space: nowrap;
	overflow: hidden;
	// before slide load end
	@include padding-horizontal(nth($slider-btn-size, 1));

	@include media('<mob-lg') {
		@include padding-horizontal(nth($slider-btn-size--sm, 1)); }

	.no-touchevents & {
		@include padding-horizontal(nth($slider-btn-size, 1)-($products-rang+$products-shade-size));

		@include media('<mob-lg') {
			@include padding-horizontal(nth($slider-btn-size--sm, 1)-($products-rang+$products-shade-size)); } }

	&, &.slick-dotted {
		margin: 0; }

	&.slick-initialized, .no-js & {
		// after slide load
		white-space: normal;
		overflow: visible;
 }		// after slide load end

	.slick-track {
